import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { VideoRecord } from "../services/types";
import { selectVideoSchedule, setVideoSchedule } from "../store/slices/app";

type Props = {
	onClose: () => void;
};

export const VideoSchedule = ({ onClose }: Props) => {
	const dispatch = useDispatch();

	const videoSchedule = useSelector(selectVideoSchedule);

	const [videoName, setVideoName] = useState("");
	const [videoTiming, setVideoTiming] = useState<number>(0);
	const [editedVideoSchedule, setEditedVideoSchedule] = useState<VideoRecord[]>(videoSchedule);
	const [bulkImportOpen, setBulkImportOpen] = useState(false);
	const [textareaText, setTextareaText] = useState("");

	return (
		<div className="stream-settings">
			<div className="stream-settings-heading">Video schedule</div>
			{!bulkImportOpen && (
				<div>
					<div className="video-schedule-list col-10 mb-3">
						<div className="container">
							<div className="stream-settings-video-list-head row mb-1">
								<div className="col-5">Media name</div>
								<div className="col-3">Start time</div>
								<div className="col-2"></div>
							</div>
							{editedVideoSchedule.map((videoRecord, index) => {
								return (
									<div className="stream-settings-video-list-item row" key={videoRecord.name}>
										<div className="col-5">{videoRecord.name}</div>
										<div className="col-3">{videoRecord.secondsFromStart}</div>
										<div className="col-4 d-flex justify-content-end">
											<button
												className="btn btn-sm btn-success"
												type="button"
												title="Remove"
												onClick={() => {
													// dispatch(postMediaPlay)
													const updatedVideoSchedule = [...editedVideoSchedule];
													updatedVideoSchedule.splice(index, 1);
													setEditedVideoSchedule(updatedVideoSchedule);
												}}
											>
												Play now
											</button>
											<button
												className="btn btn-sm btn-primary ms-3"
												type="button"
												title="Remove"
												onClick={() => {
													const updatedVideoSchedule = [...editedVideoSchedule];
													updatedVideoSchedule.splice(index, 1);
													console.log("# UPD", updatedVideoSchedule);

													setEditedVideoSchedule(updatedVideoSchedule);
												}}
											>
												<i className="bi bi-trash-fill me-0" />
											</button>
										</div>
									</div>
								);
							})}
						</div>
					</div>
					<div className="col-md-10">
						<div className="container">
							<div className="input-group row mb-3">
								<div className="col-5">
									<input
										type="text"
										className="form-control"
										value={videoName}
										onChange={(event) => setVideoName(event.target.value)}
									/>
								</div>
								<div className="col-4">
									<input
										type="number"
										className="form-control"
										value={videoTiming}
										onChange={(event) => setVideoTiming(Number(event.target.value))}
									/>
								</div>
								<div className="col-1">
									<button
										className="btn btn-outline-secondary"
										type="button"
										onClick={() => {
											if (!editedVideoSchedule.find((video) => video.name === videoName)) {
												setEditedVideoSchedule([
													...editedVideoSchedule,
													{
														name: videoName,
														secondsFromStart: videoTiming,
													},
												]);
											}
										}}
									>
										Add
									</button>
								</div>
							</div>
						</div>
					</div>

					<div className="mt-3 stream-settings-buttons">
						<button
							className="btn btn-primary"
							onClick={async () => {
								dispatch(setVideoSchedule(editedVideoSchedule) as any);
								// await dispatch(initialize(updatedLanguagesSettings) as any);
								onClose();
							}}
						>
							<i className={"bi bi-cloud-arrow-up"} />
							<span>Save</span>
						</button>
						<button
							className="btn btn-secondary btn-cancel ms-2"
							onClick={() => {
								onClose();
							}}
						>
							Cancel
						</button>
						<button
							className="btn btn-success ms-2"
							onClick={() => {
								setBulkImportOpen(true);
							}}
						>
							Bulk import
						</button>
					</div>
				</div>
			)}

			{bulkImportOpen && (
				<div>
					<div className="col-12">
						<textarea
							className="form-control"
							value={textareaText}
							rows={10}
							onChange={(event) => setTextareaText(event.target.value)}
							onBlur={() => setTextareaText(textareaText.trim())}
						/>
					</div>

					<div className="mt-3 stream-settings-buttons">
						<button
							className="btn btn-primary"
							onClick={async () => {
								const lines = textareaText.split("\n");

								const videoSchedule: VideoRecord[] = lines.map((line) => {
									const [name, secondsFromStart] = line.trim().split("\t");
									return {
										name,
										secondsFromStart: Number(secondsFromStart),
									};
								});

								setEditedVideoSchedule(videoSchedule);

								setBulkImportOpen(false);
							}}
						>
							<i className={"bi bi-cloud-arrow-up"} />
							<span>Import</span>
						</button>
						<button
							className="btn btn-secondary btn-cancel ms-2"
							onClick={() => {
								setBulkImportOpen(false);
							}}
						>
							Go back
						</button>
					</div>
				</div>
			)}
		</div>
	);
};

export default VideoSchedule;
